
import Tab from 'vux/src/components/tab/tab.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import Group from 'vux/src/components/group/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import reportService from '../service/reportService'
import saleReportService from '../service/saleRepotService'
import Loading from './loading'

const PROJ_LIST = 'ROSE_PROJ_LIST';
const PAGE_SIZE = 30;
export default {
components: {
Tab,
Cell,
Group,
TabItem,
Calendar,
CellFormPreview,
Grid,
GridItem,
PopupPicker,
Selector,
Divider,
Loading
},
data() {
return {
showTab: true, // 
activeIndex: 0,
filterDate: [], // 
reportList: [], // 
curTab: 'days',
dateList: [
{
name: '',
value: 'days'
},
{
name: '',
value: 'weeks'
},
{
name: '',
value: 'months'
},
{
name: '',
value: 'years'
},
], // 
reportData: { // 
days: [],
weeks: [],
months: [],
years: []
},
filterParams: {}, // 
showDate: false, // 
dateSelected: { // 
name: '',
value: 'days'
},
showProj: false, // 
projList: [], // 
page: 1, // 
curPage: 1, // 
objName: '', // 
isDisabled: false,
showLoading: false
}
},
methods: {
onItemClick(item) {
this.curTab = item.value;
this.reportList = this.reportData[item.value]
},
onChange(val) {
console.log(val);

},

resetReportData() {
this.reportData = {
days: [],
weeks: [],
months: [],
years: []
}
},

assembleData(params = {}) {
this.showLoading = true;
reportService.getReport(Object.assign(this.filterParams, {
objName: this.objName,
pageNo: this.page
})).then(data => {
this.showLoading = false;
this.resetReportData();
let map = ['days', 'weeks', 'months', 'years'];

map.forEach(item => {
data[item].forEach((data, index) => {
let detail = [
{
label: '',
value: `${data.qty || 0}/`
}, {
label: '',
value: `${numberComma(data.amount) || 0}`
}, {
label: 'A',
value: `${numberComma(data.aProduct) || 0}`
}, /*{
label: 'B',
value: '999'
},*/ {
label: '',
value: data.sybName || ''
}, {
label: '',
value: data.bankName || ''
}, {
label: '',
value: data.bmName || ''
},
];
if (this.objName === '') {
detail.shift();
detail.shift();
}
this.reportData[item].push({
name: `${(index + 1) + (this.page - 1) * PAGE_SIZE}. ${data.creator}`,
sales: this.objName ? `${data.qty || 0}/` : `${numberComma(data.aProduct) || 0}`,
showContent: false,
detail: detail
})
})
});
this.curPage = this.page;
this.reportList = this.reportData[this.dateSelected.value];
this.isDisabled = this.reportList.length < PAGE_SIZE
}).catch(err => {
this.showLoading = false;
this.resetReportData();
this.reportList = [];
this.$vux.alert.show({
content: err.message
})
})
},

getProj() {
let proj = JSON.parse(sessionStorage.getItem(PROJ_LIST));
proj.shift();
this.projList = proj
},

hideDropList() {
this.showDate = false;
this.showProj = false;
},

dateClick() {
this.showDate = !this.showDate;
this.showProj = false;
},

dateItemClick(item) {
this.showDate = false;
if (item.value === this.dateSelected.value) {
console.log('');
return
}
this.dateSelected = item;
this.reportList = this.reportList.map(item => {
return Object.assign(item, {
showContent: false
})
});
if (this.page === 1) {
this.reportList = this.reportData[item.value];
} else {
this.page = 1;
this.assembleData()
}
},

projClick() {
this.showProj = !this.showProj;
this.showDate = false;
},

projItemClick(item, index) {
this.showProj = false;
if (item === this.objName) {
console.log('');
return
}
this.objName = item;
this.page = 1;
this.assembleData()
},

aProjClick() {
this.showProj = false;
this.showDate = false;
this.objName = '';
this.page = 1;
this.assembleData();
},

pagePrev() {
if (this.page === 1) {
return
}
this.page--;
this.assembleData();
},

pageNext() {
if (this.isDisabled) {
return
}
this.page++;
this.assembleData();
},
},
filters: {

nameFilter(val) {
return val.length > 5 ? `${val}...` : val || ''
}
},
created() {
let query = this.$route.query;
this.filterParams = {
shengName: query.region ? decodeURI(query.region) : '', // 
bankName: query.bank ? decodeURI(query.bank) : '', // 
bmName: query.dept ? decodeURI(query.dept) : '', // 
objName: query.proj ? decodeURI(query.proj) : '', // 
}

if (this.filterParams.objName) {
this.objName = this.filterParams.objName;
}
this.getProj()
},
mounted(){
this.assembleData(this.filterParams);

}
}



